import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';
import { SkillsComponent } from './skills/skills.component';
import { ExperienceComponent } from './experience/experience.component';
import { EducationComponent } from './education/education.component';
import { ProjectsComponent } from './projects/projects.component';
import { IconSetsComponent } from './icon-sets/icon-sets.component';
import { PrototypingToolComponent } from './prototyping-tool/prototyping-tool.component';
import { CaseStudyComponent } from './case-study/case-study.component';

const routes: Routes = [

  { path: '', component: HomeComponent },
  { path: 'home', component: HomeComponent },
  { path: 'about', component: AboutComponent },
  { path: 'skills', component: SkillsComponent },
  { path: 'experience', component: ExperienceComponent },
  { path: 'skills', component: SkillsComponent },
  { path: 'education', component: EducationComponent },
  { path: 'projects', component: ProjectsComponent },
  { path: 'icon-sets', component: IconSetsComponent },
  { path: 'casestudy', component: CaseStudyComponent },
  { path: 'prototyping-tool', component: PrototypingToolComponent },
  // {path: '404', component: NotFoundComponent},
  {path: '**', redirectTo: '/home'}
];

@NgModule({
  declarations: [
    HomeComponent,
    AboutComponent,
    SkillsComponent,
    ExperienceComponent,
    EducationComponent,
    ProjectsComponent,
    IconSetsComponent,
    PrototypingToolComponent,
    CaseStudyComponent,
  ],
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
